/* @import url('https://fonts.googleapis.com/css2?family=Anta&family=Bruno+Ace&display=swap'); */
input {
    width: 40vw;
    border-radius: 6px;
    text-align: center;
    box-shadow: inset 3px 3px 12px 0px black;
    background-color: lightgray;
    border: none;
    color: black;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: calc(8px + 1vmin);
    caret-color: rgb(0, 179, 255);
    margin: 8px;
    height: calc(10px + 4vmin);
    min-height: 30px;
}


input:focus { 
    outline: none;
}
