@import url(https://fonts.googleapis.com/css2?family=Anta&family=Bruno+Ace&display=swap);
*{
  margin: 0 
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  min-height: 100vh;
  min-width: 100vw;
  background-image: url(/task-redux/static/media/metal-dark.be0fe0f6.jpeg);
  background-size: cover;
  display: grid;
  place-items: center;
}

h1 {
  letter-spacing: 10px;
  font-family: "Anta", sans-serif;
  font-size: calc(8px + 2vmin);
  color: #00aeff;
  /* text-shadow: 0 0 10px rgba(0, 174, 255, 0.5), 0 0 20px rgba(0, 174, 255, 0.5), 0 0 30px rgba(0, 174, 255, 0.5), 0 0 40px rgba(0, 174, 255, 0.5); */
  -webkit-animation: flicker 3s infinite alternate;
          animation: flicker 3s infinite alternate;  
}

@-webkit-keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
  /* 0%, 18%, 25%, 53%, 100% { */
    text-shadow:
      /* 0 0 1px #00aeff, */
      0 0 2px #00aeff,
      /* 0 0 3px #00aeff, */
      0 0 6px #00aeff,
      /* 0 0 10px #00aeff; */
      /* 0 0 12px #00aeff, */
      /* 0 0 18px #00aeff; */
      0 0 24px #00aeff;
  }

  20%, 24%, 55% {        
    text-shadow: none;
  }    
}

@keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
  /* 0%, 18%, 25%, 53%, 100% { */
    text-shadow:
      /* 0 0 1px #00aeff, */
      0 0 2px #00aeff,
      /* 0 0 3px #00aeff, */
      0 0 6px #00aeff,
      /* 0 0 10px #00aeff; */
      /* 0 0 12px #00aeff, */
      /* 0 0 18px #00aeff; */
      0 0 24px #00aeff;
  }

  20%, 24%, 55% {        
    text-shadow: none;
  }    
}

.app_container {
  height: 80vh;
  width: 50vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: radial-gradient(circle, rgba(89, 89, 89, 1) 0%, rgba(20, 20, 20, 1) 100%);
  box-shadow: inset 6px 6px 14px 0px black;
  border-radius: 12px;
  padding: 32px 0;
  margin-top: none;
}

.app_todoContainer {
  flex: 1 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.koa {
  position: absolute;
  right: 0;
  bottom: 0;
  height: calc(12px + 3vmin);
  width: calc(12px + 3vmin);
  line-height: calc(12px + 3vmin);
  font-size: calc(2px + 1vmin);
  border-top-left-radius: 8px;
  text-align: center;
  box-shadow: inset 2px 2px 6px 0px black;
  font-family: "Anta", sans-serif;
  font-weight: bolder;
  background: radial-gradient(ellipse at center, #00AAFF, #07536c);
  color: beige;
}
/* @import url('https://fonts.googleapis.com/css2?family=Anta&family=Bruno+Ace&display=swap'); */
input {
    width: 40vw;
    border-radius: 6px;
    text-align: center;
    box-shadow: inset 3px 3px 12px 0px black;
    background-color: lightgray;
    border: none;
    color: black;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: calc(8px + 1vmin);
    caret-color: rgb(0, 179, 255);
    margin: 8px;
    height: calc(10px + 4vmin);
    min-height: 30px;
}


input:focus { 
    outline: none;
}

.todoItem {
    display: flex;
    align-items: center;
    width: 90%;
    height: calc(10px + 4vmin);
    max-height: 40px;
    margin: 6px 4px;
    border-radius: 4px;
    background: linear-gradient(180deg, rgba(235,235,235,1) 0%, rgb(106, 106, 106) 100%);
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

/* .todoItem>p {
    flex: 1;
} */

.description{
    margin-right: 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: calc(6px + 1vmin);
}

.todoItem--done {
    text-decoration: line-through;
}

.butt {
    margin-left: auto;
}

.delete {
    border-radius: 4px;
    text-shadow:0 1px 0 #666;
    text-align:center;
    text-decoration:none;
    background: linear-gradient(180deg, rgb(146, 156, 165) 0%, rgb(84, 84, 84) 100%);
    opacity:0.95;
    border: none;
    margin-right: 1vmin;
    margin-left: auto;
    width: calc(10px + 4vmin);
    transition: all 0.3s ease;
    color: lightgray;
    font-size:calc(12px + 1vmin);
    font-weight: 900;
    height: calc(8px + 2vmin);
}
    
.delete:hover{
    text-shadow:0 0 1px #666;
    text-decoration:none;
    box-shadow:0 0 4px #222 inset;
    color: #00b3ff;
    background-color: rgb(73, 73, 73);
}
