.todoItem {
    display: flex;
    align-items: center;
    width: 90%;
    height: calc(10px + 4vmin);
    max-height: 40px;
    margin: 6px 4px;
    border-radius: 4px;
    background: linear-gradient(180deg, rgba(235,235,235,1) 0%, rgb(106, 106, 106) 100%);
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

/* .todoItem>p {
    flex: 1;
} */

.description{
    margin-right: 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: calc(6px + 1vmin);
}

.todoItem--done {
    text-decoration: line-through;
}

.butt {
    margin-left: auto;
}

.delete {
    border-radius: 4px;
    text-shadow:0 1px 0 #666;
    text-align:center;
    text-decoration:none;
    background: linear-gradient(180deg, rgb(146, 156, 165) 0%, rgb(84, 84, 84) 100%);
    opacity:0.95;
    border: none;
    margin-right: 1vmin;
    margin-left: auto;
    width: calc(10px + 4vmin);
    transition: all 0.3s ease;
    color: lightgray;
    font-size:calc(12px + 1vmin);
    font-weight: 900;
    height: calc(8px + 2vmin);
}
    
.delete:hover{
    text-shadow:0 0 1px #666;
    text-decoration:none;
    box-shadow:0 0 4px #222 inset;
    color: #00b3ff;
    background-color: rgb(73, 73, 73);
}