@import url('https://fonts.googleapis.com/css2?family=Anta&family=Bruno+Ace&display=swap');

.App {
  min-height: 100vh;
  min-width: 100vw;
  background-image: url(./metal-dark.jpeg);
  background-size: cover;
  display: grid;
  place-items: center;
}

h1 {
  letter-spacing: 10px;
  font-family: "Anta", sans-serif;
  font-size: calc(8px + 2vmin);
  color: #00aeff;
  /* text-shadow: 0 0 10px rgba(0, 174, 255, 0.5), 0 0 20px rgba(0, 174, 255, 0.5), 0 0 30px rgba(0, 174, 255, 0.5), 0 0 40px rgba(0, 174, 255, 0.5); */
  animation: flicker 3s infinite alternate;  
}

@keyframes flicker {
  0%, 18%, 22%, 25%, 53%, 57%, 100% {
  /* 0%, 18%, 25%, 53%, 100% { */
    text-shadow:
      /* 0 0 1px #00aeff, */
      0 0 2px #00aeff,
      /* 0 0 3px #00aeff, */
      0 0 6px #00aeff,
      /* 0 0 10px #00aeff; */
      /* 0 0 12px #00aeff, */
      /* 0 0 18px #00aeff; */
      0 0 24px #00aeff;
  }

  20%, 24%, 55% {        
    text-shadow: none;
  }    
}

.app_container {
  height: 80vh;
  width: 50vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: radial-gradient(circle, rgba(89, 89, 89, 1) 0%, rgba(20, 20, 20, 1) 100%);
  box-shadow: inset 6px 6px 14px 0px black;
  border-radius: 12px;
  padding: 32px 0;
  margin-top: none;
}

.app_todoContainer {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.koa {
  position: absolute;
  right: 0;
  bottom: 0;
  height: calc(12px + 3vmin);
  width: calc(12px + 3vmin);
  line-height: calc(12px + 3vmin);
  font-size: calc(2px + 1vmin);
  border-top-left-radius: 8px;
  text-align: center;
  box-shadow: inset 2px 2px 6px 0px black;
  font-family: "Anta", sans-serif;
  font-weight: bolder;
  background: radial-gradient(ellipse at center, #00AAFF, #07536c);
  color: beige;
}